html, body, #root {
  height: 100%;
  width:100%;
}

li {
  margin:10px;
}

.App-title {
  cursor: pointer;
  color: white;
  font-family: 'Rubik-mono', serif;
  font-weight: 100;
}

.Screen-contentstitle {
  text-align: center;
}

@media (min-width: 800px) {
  .App-header {
    width:100%;
    height:5%;
    padding-bottom:5%;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(90, 161, 255, 0.5)),color-stop(40%, rgba(255, 3, 3, 0)));
    background: -webkit-linear-gradient(top, rgba(90, 161, 255, 0.5),rgba(255, 3, 3, 0) 40%);
    background: -o-linear-gradient(top, rgba(90, 161, 255, 0.5),rgba(255, 3, 3, 0) 40%);
    background: linear-gradient(to bottom, rgba(90, 161, 255, 0.5),rgba(255, 3, 3, 0) 40%);
  }

  .App {
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
  }

  .App-bg {
    position:absolute;
    background-image: url("bluewirebackground.png");
    -webkit-filter: blur(5px);
            filter: blur(5px);
    width:100%;
    height:100%;
    z-index:-1;
  }

  .Screen-window {
    height: 90%;
    width: 100%;
    overflow-y: scroll;
    color: white;
  }

  .Screen-contents {
    width:70%;
    margin-left: 10%;
    padding: 0.5% 5% 0.5% 5%;
    background-color: rgba(2, 2, 2, 0.6);
  }

  .App-title {
    font-size: 30px;
    width:20%;
    height:10%;
    position:absolute;
    float:left;
  }

  .App-title:hover {
    color: rgba(90, 161, 255, 0.9);
  }

  .App-headerbuttons {
    position:absolute;
    margin-left: 20%;
    margin-top:3%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width:60%;
    height:2%;
  }
  
  .Button-link {
    cursor: pointer;
    color: rgb(255, 255, 255);
    width: 100%;
    height: 100%;
    font-family: 'Heebo-bold', sans-serif;
    font-size:12px;
    font-weight: 200;
    margin-left:10px;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .Button-link:hover {
    background-color: rgba(90, 161, 255, 0.8);
  }
}
@media (max-width: 799px) {
  .App-header {
    margin-right: 5%;
    width:20%;
    height:100%;
    color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .App {
    height: 100%;
    width:100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }

  .Screen-window {
    height: 100%;
    width: 80%;
    background:-webkit-gradient(linear,left top, left bottom,from(#EBDEF6), to(white));
    background:-webkit-linear-gradient(top,#EBDEF6, white);
    background:-o-linear-gradient(top,#EBDEF6, white);
    background:linear-gradient(180deg,#EBDEF6, white);
    overflow-y: scroll;
  }

  .App-title {
    font-size: 20px;
  }

  .App-headerbuttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  
  .Button-link {
    cursor: pointer;
    color: rgb(54, 54, 54);
    width: 100%;
    margin: 10% 0 10% 2%;
    font-family: 'Heebo-bold', sans-serif;
    font-size:16px;
    font-weight: bolder;
    text-shadow: 0px 2px #8b8b8b, 0px 1px 2px rgb(0, 0, 0);
  }
}

@font-face {
  font-family: 'Heebo-bold';
  src: local('Heebo-bold'), url(./fonts/Heebo-Bold.ttf) format('.ttf');
}

@font-face {
  font-family: "Rubik-mono";
  src: url("./fonts/RubikMonoOne-Regular.ttf");
}

@font-face {
  font-family: "Sunflower";
  src: url("./fonts/Sunflower-Light.ttf");
}


